<template>
    <div class="f-treetable panel panel-success">
        <div class="panel-heading" style="text-align:left;">
            CrossReferences
        </div>
        <div class="panel-body">
            <div class="yz-treetable">
                <table class="table table-bordered">
                    <colgroup>
                        <col width="100px">
                        <col>
                        <col width="300px">
                        <col width="300px">
                        <col width="300px">
                    </colgroup>
                    <thead>
                        <tr>
                            <th>序号</th>
                            <th>服务名称</th>
                            <th>地址</th>
                            <th>状态</th>
                            <th>操作</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, $index) in dealData">
                            <td style="text-align:left;">
                                <div class="table-tree-icon" :style="iconStyle(item)" @click="toggleChildren(item,$index)">
                                    <i class="yz-icon" :class="iconClass(item)"></i>
                                </div>
                            </td>
                            <td>{{item.serviceName}}</td>
                            <td>{{item.address}}</td>
                            <td>{{item.status}}</td>
                            <td>
                                <div class="btn-icon" title="删除" @click="handleDeleteRow(item)">
                                    <i class="icon iconfont icon-shanchu"></i>
                                </div>
                            </td>
                        </tr>
                        <tr v-if="dealData.length == 0">
                            <td colspan="5">
                                没有数据
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "treeTable",
    componentName: "treeTable",
    props: {
        // data: Array
    },
    data() {
        return {
            dealData: [],
            data: []
        }
    },
    watch: {

    },
    mounted: function() {
        console.log('treeTable', this);
        this.getData();

    },
    methods: {
        getData: function() {
            var _this = this;
            this.$http({
                method: 'post',
                url: '/queryCrossReferences.do',
                data: {}

            }).then(function(resp) {
                console.log(resp);
                if(resp.status !== 200){
                    _this.$message({
                        message:"加载失败",
                        type:"error"
                    });
                }

                var result = resp.data;
                if(result.length == 0 || result[1].serviceName === null){
                    result = [];
                }
                _this.data = result;
                _this.dealData = result;
                _this.setParent(result, 1, -1);
            })
        },

        handleDeleteRow: function(item) {
            var obj = {
                serviceName: item.serviceName,
                serviceNodeAddr: item.serviceNodeAddr ? item.serviceNodeAddr : undefined
            };
            $.ajax({
                url: "http://localhost:10000/monitor/delServiceNode.do",
                type: "post",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json"
                },
                data: JSON.stringify(obj),
                dataType: "json",
                success: function success(e) {
                    //console.log(e);

                    _this.data = e;
                    _this.dealData = e;
                    _this.setParent(_this.data, 1, -1);
                },
                error: function error() {
                    console.log("更新用户服务请求失败！");
                }
            });
        },




        setParent: function(data, level, n) {
            console.log(data, level, n);
            for (var a = data.length, r = 0; r < a; r++) {
                var item = data[r];
                item.checkbox = false;
                item.currentLevel = level;
                item.parentIndex = -1 == n ? null : n;
                if (item.nodeList && 0 != item.nodeList.length) {
                    var l = -1 === n ? r : item.parentIndex + "-" + r;
                    this.setParent(item.nodeList, level + 1, l)
                }
            }
        },
        iconStyle: function(e) {
            return {
                marginLeft: 10 * e.currentLevel + "px"
            }
        },
        iconClass: function(e) {
            return e.nodeList && 0 != e.nodeList.length ? e.isOpen ? {
                "icon-open": !0
            } : {
                "icon-close": !0
            } : {
                "icon-default": !0
            }
        },
        toggleChildren: function(e, t) {
            console.log("toggle", e, t);
            e.nodeList && 0 != e.nodeList.length && (e.isOpen ? this.closeRow(e, t) : this.openRow(e, t))
        },
        openRow: function(e, t) {
            if (!e.isOpen) {
                for (var n = 0; n < e.nodeList.length; n++) {
                    this.dealData.splice(t + n + 1, 0, e.nodeList[n]);
                }
                e.isOpen = true;
            }
        },
        closeRow: function(row, index) {
            console.log("-----", index, spliceLength);
            var spliceLength = 0;
            if (row.isOpen) {
                for (var i = index + 1, ilength = this.dealData.length; i < ilength; i++) {
                    var item = this.dealData[i];
                    if (item.currentLevel == row.currentLevel) {
                        spliceLength = i - index - 1;
                        break;
                    }
                    if (item.isOpen = true) {
                        item.isOpen = false;
                    }
                }
                console.log("-----", index, spliceLength);
                this.dealData.splice(index + 1, spliceLength);
                console.log(this.dealData)
                item.isOpen = false;
            }
            row.isOpen = false;
            // if (row.isOpen) {
            //     for (var n = e.currentLevel, a = 0, r = t + 1, o = this.dealData.length; r < o; r++) {
            //         var l = this.dealData[r];
            //         if (l.isOpen = !1, console.log(l.currentLevel >= n), l.currentLevel <= n) {
            //             a = r - t - 1;
            //             break
            //         }
            //     }
            //     console.log(a), this.dealData.splice(t + 1, a), e.isOpen = !1
            // }
        }
    }
}
</script>
<style>
.pc-header {
    height: 60px
}

.f-treetable .td {
    text-align: left;
}

.f-treetable .table-tree-icon {
    display: inline-block;
    margin-left: 10px;
}

.f-treetable .yz-icon {
    position: relative;
    display: inline-block;
    height: 18px;
    width: 18px;
}

.f-treetable .yz-icon.icon-close {
    background: url("/static/img/server/folderClose.gif") 2px -1px no-repeat
}

.f-treetable .yz-icon.icon-open {
    background: url("/static/img/server/folderOpen.gif") 2px -1px no-repeat
}

.f-treetable .yz-icon.icon-default {
    background: url("/static/img/server/defaultLeaf.gif") 2px -1px no-repeat
}

.btn-icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    text-align: center;
}

.btn-icon:hover {
    color: #20a0ff;
}
</style>



